import React, { ReactNode, useContext } from "react";
import { MDXProvider } from "@mdx-js/react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import {
  vscDarkPlus,
  oneLight,
} from "react-syntax-highlighter/dist/esm/styles/prism";
import styled, { ThemeContext } from "styled-components";
import { darkTheme } from "./theme";

const InlineCode = styled.code`
  background-color: ${props => props.theme.neutral};
  color: ${props => props.theme.text};
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-family: "Fira Code", monospace;
  font-size: 0.9em;
`;

const CodeBlock = styled.div`
  position: relative;
  margin: 2rem 0;
  border-radius: 8px;
  box-shadow: ${props =>
    props.theme.body === darkTheme.body
      ? "0 4px 8px rgba(0, 0, 0, 0.2)"
      : "0 2px 6px rgba(0, 0, 0, 0.1)"};
`;

const Language = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  color: ${props => (props.theme.body === darkTheme.body ? "#e6e6e6" : "#666")};
  font-size: 0.8rem;
  background: ${props =>
    props.theme.body === darkTheme.body
      ? "rgba(0, 0, 0, 0.3)"
      : "rgba(0, 0, 0, 0.1)"};
  border-radius: 0 8px 0 8px;
  padding: 0.2rem 0.5rem;
  text-transform: uppercase;
  font-weight: 500;
`;

interface PreProps {
  children: React.ReactElement;
}

interface CodeProps {
  children: ReactNode;
  className?: string;
}

const CodeComponent = (props: CodeProps) => {
  const { children, className } = props;
  const language = className ? className.replace(/language-/, "") : "";
  const theme = useContext(ThemeContext);
  const isDarkMode = theme.body === darkTheme.body;

  if (!language) {
    return <InlineCode>{children}</InlineCode>;
  }

  return (
    <CodeBlock>
      {language && <Language>{language}</Language>}
      <SyntaxHighlighter
        language={language}
        style={isDarkMode ? vscDarkPlus : oneLight}
        showLineNumbers={true}
        wrapLines={true}
        customStyle={{
          margin: 0,
          borderRadius: "8px",
          padding: "1.5rem 1rem",
          fontSize: "0.9rem",
          fontFamily: "'Fira Code', monospace",
          backgroundColor: isDarkMode ? undefined : "#f8f8f8",
        }}
        lineNumberStyle={{
          color: isDarkMode ? "#6272a4" : "#999",
          opacity: 0.6,
        }}
      >
        {String(children).trim()}
      </SyntaxHighlighter>
    </CodeBlock>
  );
};

const components = {
  pre: (props: PreProps) => props.children,
  code: CodeComponent,
};

interface MDXProviderProps {
  children: ReactNode;
}

export const MDXProviderWrapper: React.FC<MDXProviderProps> = ({
  children,
}) => {
  return <MDXProvider components={components}>{children}</MDXProvider>;
};
