import React from "react";
import { graphql } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import styled from "styled-components";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { MDXProviderWrapper } from "../components/mdx-provider";

interface BlogPostProps {
  data: {
    mdx: {
      body: string;
      frontmatter: {
        title: string;
        date: string;
        author: string;
        description: string;
        tags?: string[];
      };
      fields: {
        slug: string;
      };
    };
  };
}

const PostContainer = styled.article`
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem 1rem;

  @media (min-width: 768px) {
    padding: 4rem 2rem;
  }
`;

const PostHeader = styled.header`
  margin-bottom: 4rem;
  text-align: center;
  position: relative;

  @media (min-width: 768px) {
    margin-bottom: 6rem;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -2rem;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: ${props => props.theme.accent};
  }
`;

const PostTitle = styled.h1`
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: ${props => props.theme.text};
  font-weight: 800;
  letter-spacing: -0.03em;
  line-height: 1.2;

  @media (min-width: 768px) {
    font-size: 3.5rem;
  }
`;

const PostMeta = styled.div`
  color: ${props => props.theme.neutralDark};
  font-size: 1rem;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  @media (min-width: 768px) {
    flex-direction: row;
    font-size: 1.1rem;
    gap: 1.5rem;
  }

  span {
    padding: 0.5rem 1rem;
    background: ${props => props.theme.neutral};
    border-radius: 20px;
    font-weight: 500;
  }
`;

const PostContent = styled.div`
  line-height: 1.8;
  color: ${props => props.theme.text};
  font-size: 1.1rem;

  @media (min-width: 768px) {
    font-size: 1.2rem;
  }

  h2 {
    font-size: 1.8rem;
    margin: 3rem 0 1.5rem;
    color: ${props => props.theme.text};
    font-weight: 700;
    letter-spacing: -0.02em;
    line-height: 1.3;

    @media (min-width: 768px) {
      font-size: 2.2rem;
      margin: 4rem 0 2rem;
    }
  }

  h3 {
    font-size: 1.5rem;
    margin: 2.5rem 0 1.25rem;
    color: ${props => props.theme.text};
    font-weight: 600;
    line-height: 1.4;

    @media (min-width: 768px) {
      font-size: 1.8rem;
      margin: 3rem 0 1.5rem;
    }
  }

  p {
    margin-bottom: 1.5rem;
    line-height: 1.8;

    @media (min-width: 768px) {
      margin-bottom: 2rem;
      line-height: 1.9;
    }
  }

  code {
    background-color: ${props => props.theme.neutral};
    color: ${props => props.theme.text};
    padding: 0.2em 0.4em;
    border-radius: 4px;
    font-family: "Fira Code", monospace;
    font-size: 0.9em;
  }

  pre {
    background-color: ${props => props.theme.neutral};
    border-radius: 8px;
    padding: 1rem;
    margin: 1.5rem 0;
    overflow-x: auto;
    font-size: 0.9em;
    line-height: 1.6;

    @media (min-width: 768px) {
      border-radius: 12px;
      padding: 1.5rem;
      margin: 2.5rem 0;
    }

    code {
      background-color: transparent;
      padding: 0;
      font-size: inherit;
    }
  }

  ul,
  ol {
    margin: 1.5rem 0;
    padding-left: 1.5rem;

    @media (min-width: 768px) {
      margin: 2rem 0;
      padding-left: 2rem;
    }
  }

  li {
    margin-bottom: 0.75rem;
    line-height: 1.7;

    @media (min-width: 768px) {
      margin-bottom: 1rem;
      line-height: 1.8;
    }

    &::marker {
      color: ${props => props.theme.accent};
    }
  }

  a {
    color: ${props => props.theme.accent};
    text-decoration: none;
    border-bottom: 1px solid transparent;
    transition: border-color 0.2s ease;

    &:hover {
      border-color: ${props => props.theme.accent};
    }
  }

  blockquote {
    border-left: 3px solid ${props => props.theme.accent};
    margin: 2rem 0;
    padding: 1rem 1.5rem;
    background-color: ${props => props.theme.neutral};
    border-radius: 0 8px 8px 0;
    font-style: italic;
    position: relative;

    @media (min-width: 768px) {
      margin: 3rem 0;
      padding: 1.5rem 2rem;
      border-radius: 0 12px 12px 0;
    }

    p {
      margin: 0;
      font-size: 1.1rem;
      line-height: 1.7;
      color: ${props => props.theme.text};

      @media (min-width: 768px) {
        font-size: 1.3rem;
      }
    }
  }

  img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin: 1.5rem 0;

    @media (min-width: 768px) {
      border-radius: 12px;
      margin: 3rem 0;
    }
  }

  hr {
    border: none;
    border-top: 1px solid ${props => props.theme.neutral};
    margin: 2rem 0;

    @media (min-width: 768px) {
      margin: 4rem 0;
    }
  }
`;

const BlogPost = ({ data }: BlogPostProps) => {
  const post = data.mdx;
  const { title, date, author, description, tags = [] } = post.frontmatter;

  return (
    <Layout>
      <SEO
        title={title}
        description={description}
        pathname={post.fields.slug}
        article={true}
        publishedTime={date}
        modifiedTime={date}
        tags={tags}
      />
      <PostContainer>
        <PostHeader>
          <PostTitle>{title}</PostTitle>
          <PostMeta>
            <span>{author}</span>
            <span>{new Date(date).toLocaleDateString()}</span>
            {tags && tags.length > 0 && (
              <span>
                {tags.map((tag, index) => (
                  <React.Fragment key={tag}>
                    {tag}
                    {index < tags.length - 1 && ", "}
                  </React.Fragment>
                ))}
              </span>
            )}
          </PostMeta>
        </PostHeader>
        <PostContent>
          <MDXProviderWrapper>
            <MDXRenderer>{post.body}</MDXRenderer>
          </MDXProviderWrapper>
        </PostContent>
      </PostContainer>
    </Layout>
  );
};

export const query = graphql`
  query BlogPostBySlug($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      frontmatter {
        title
        date
        author
        description
        tags
      }
      fields {
        slug
      }
    }
  }
`;

export default BlogPost;
